/* General */

@import url('https://fonts.googleapis.com/css?family=Nanum+Gothic');

* {
  margin: 0;
}

body {
  height: 100%;
  overflow: auto;
  background-size: cover;
  background-repeat: no-repeat;
  font-family: 'Nanum Gothic', sans-serif;
}

html {
  overflow: hidden;
  height: 100%;
}

input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}

/* Fonts */

.hero h1 {
  font-size: 50px;
  color: #373C51;
  text-align: center;
  padding: 182px 0 0 0;
}

.hero h2 {
  font-size: 18px;
  text-align: center;
  color: #3a434c;
  background: -webkit-linear-gradient(to left, #BBD2C5 90%, #536976);
  background: -webkit-gradient(linear, right top, left top, color-stop(90%, #BBD2C5), to(#536976));
  background: -webkit-linear-gradient(right, #BBD2C5 90%, #536976);
  background: -o-linear-gradient(right, #BBD2C5 90%, #536976);
  background: linear-gradient(to left, #BBD2C5 90%, #536976);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.wrapper h2 {
  font-size: 30px;
  color: #536976;
  padding: 0 0 30px 0;
}

.footer h6 {
  font-size: 14px;
  color: #536976;
  padding: 25px 0 0 0;
}

p a {
  color: #536976;
  text-decoration: none;
  font-weight: bold;
}

p a:hover {
  color: #BBD2C5;
}

h6 a {
  color: #BBD2C5;
  text-decoration: none;
}

h6 a:hover {
  color: #536976;
}

p {
  font-size: 16px;
  color: #373C51;
  line-height: 25px;
  padding: 20px 0 0 0;
}

/* To all components */

.wrapper {
  margin: auto;
  width: 800px;
}

/* Hero */

.hero {
  height: 500px;
  background: #BBD2C5;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #292E49, #536976, #BBD2C5);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, right top, left top, from(#292E49), color-stop(#536976), to(#BBD2C5));
  background: -webkit-linear-gradient(right, #292E49, #536976, #BBD2C5);
  background: -o-linear-gradient(right, #292E49, #536976, #BBD2C5);
  background: linear-gradient(to left, #292E49, #536976, #BBD2C5); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

/* About */

.about {
  padding: 120px 0;
}

/* Projects */

.projects {
  padding: 0 0 120px 0;
}

/* Footer */

.footer {
  background: #292E49;
  text-align: center;
}

.footer-icon {
  height: 40px;
  padding: 25px 5px;
  opacity: .8;
}

.footer-icon:hover {
  opacity: 1;
}



